import {childElementByTag} from './dom';
import {dev} from './log';

/**
 * @param {!HTMLBodyElement} bodyElement
 * @return {!Element}
 */
export function getFixedContainer(bodyElement) {
  return dev().assertElement(childElementByTag(
      dev().assertElement(bodyElement), 'amp-ad-banner'));
}
